import Link from 'next/link';
import 'styles.css';
import Bar from 'components/stickyBar.js';
import Dropdown from 'components/dropdown.js';
import Layout from 'components/layout.js';
import Helmet from 'react-helmet';

class About extends React.Component{

    render(){

        const courses = <div id='courses'>
            <ul>
                <li>Data structures</li>
                <li>Basic Algorithms</li>
                <li>Computer System Organization</li>
                <li>Applied Internet Technology</li>
                <li>Predictive Analytics</li>
            </ul>
            <ul>
                <li>Linear Algebra</li>
                <li>Theory of Probability</li>
                <li>Ordinary Differential Equations</li>
                <li>Math of Finance</li>
            </ul>
        </div>

        const style = <style jsx>{`
            #test{
                width:200px;
                height:200px;
            }

            #courses div{
                display:inline-block;
            }


        `}</style>

        const resume =             
        <Link href="/resume.pdf">
            <a tareget="_blank">My Resume</a>
        </Link>

        return <div ref={ref=>this.test=ref}>
            {style}
            <Bar/>
            <Layout >
                <Helmet>
                    <title>About</title>
                </Helmet>
                <h1>About Me</h1>
                <Dropdown title="Relevant Coursework" info = {courses}/>
                {resume}
            </Layout>
        </div>;
    }

}

export default About;